export function visibilityClass(input) {
    let visibilityClassResult = '';
    if (input.desktop)
        visibilityClassResult += ' xb-hide-desktop';
    if (input.tablet)
        visibilityClassResult += ' xb-hide-tablet';
    if (input.mobile)
        visibilityClassResult += ' xb-hide-mobile';
    return visibilityClassResult;
}
